<template>
  <div class="dashboard">
    <h1 class="v-subheader grey--text" >Dashboard</h1>
    <v-container class="my-5">
      <v-row class="mb-3 ml-3">
        <v-tooltip top>
          <template v-slot:activator="{ on, attrs }">
          <v-btn v-bind="attrs" v-on="on"  small text color="grey" @click="sortBy('title')">
            <v-icon left small>mdi-folder</v-icon>
            <span class="caption text-lowercase">By Project Name</span>
          </v-btn>
          </template>
          <span>Sort project by project name</span>
        </v-tooltip>

        <v-tooltip top>
          <template v-slot:activator="{on, attrs}">
            <v-btn v-on="on" v-bind="attrs" small text color="grey" @click="sortBy('person')">
              <v-icon left small>mdi-account</v-icon>
              <span class="caption text-lowercase">By Person</span>
            </v-btn>
          </template>
          <span>Sort projects by person name</span>
        </v-tooltip>
      </v-row>

      <v-card flat v-for="project in projects" :key="project.title">
        <v-row no-gutters :class="`pa-3 project ${project.status}`">
          <v-col cols="12" sm="12" md="6">
            <div class="caption grey--text">Project Title</div>
            <div>{{project.title}}</div>
          </v-col>
          <v-col cols="6" sm="4" md="2">
            <div class="caption grey--text">Person</div>
            <div>{{project.person}}</div>
          </v-col>

          <v-col cols="6" sm="4" md="2">
            <div class="caption grey--text">Due by</div>
            <div>{{project.due}}</div>
          </v-col>

          <v-col cols="2" sm="4" md="2">
            <div class="float-right">
              <v-chip small :class="`${project.status} white--text caption my-2`">{{project.status}}</v-chip>

            </div>
          </v-col>
        </v-row>
        <v-divider></v-divider>
      </v-card>
    </v-container>
  </div>
</template>

<script>
  import db from '@/fb'
export default {
    data(){
        return{
            projects: []
        }
    },
    methods: {
        sortBy(prop){
            this.projects.sort((a,b) => a[prop] < b[prop] ? -1 : 1)
        }
    },
    created(){
        db.collection('projects').onSnapshot(res => {
            const changes = res.docChanges();
            changes.forEach(change => {
                if (change.type === 'added'){
                    this.projects.push({
                        ...change.doc.data(),
                        id: change.doc.id
                    })
                }
            })
        })
    }

}
</script>

<style>
  .project.complete{
    border-left: 4px solid forestgreen;
  }

  .project.ongoing{
    border-left: 4px solid orange;
  }

  .project.overdue{
    border-left: 4px solid tomato;
  }

  .v-chip.complete{
    background: forestgreen !important;
  }
  .v-chip.ongoing{
    background: orange !important;
  }
  .v-chip.overdue{
    background: tomato !important;
  }


</style>
